.sr-only, .screen-reader-text {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  margin: -1px;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  border: 0; }

.sr-only-focusable:active, .screen-reader-text:active, .sr-only-focusable:focus, .screen-reader-text:focus {
  position: static;
  width: auto;
  height: auto;
  margin: 0;
  overflow: visible;
  clip: auto; }

/** Import theme styles */
/* base */
html {
  position: relative;
  min-height: 100%; }

@media only screen and (max-width: 992px) {
  html {
    font-size: 15px; } }

body {
  background-color: #fafafa;
  color: #333;
  font-family: 'Noto Sans JP', 'Avenir', 'Helvetica Neue', 'Helvetica', 'Arial', 'Hiragino Sans', 'ヒラギノ角ゴシック', YuGothic, 'Yu Gothic', 'メイリオ', Meiryo, 'ＭＳ Ｐゴシック', 'MS PGothic', sans-serif;
  font-size: 1rem;
  font-weight: 300; }
  body.home {
    background-color: #fff; }

@media only screen and (max-width: 768px) {
  body {
    font-weight: 400; } }

html,
body {
  margin: 0; }

.wrap {
  padding-top: 20px;
  min-height: 100%;
  margin-bottom: -50px; }

.home .wrap {
  padding-top: 30px; }

@media only screen and (max-width: 768px) {
  .home .wrap {
    padding-top: 0; } }

@media only screen and (max-width: 768px) {
  .main,
  .sidebar {
    padding: 0; } }

.footer,
.push {
  height: 50px; }

a {
  color: #00081a;
  text-decoration: none; }
  a:hover {
    color: #666;
    text-decoration: underline; }

h1 {
  color: #000;
  letter-spacing: 1.8px; }

h2,
h3,
h4,
h5,
h6 {
  color: #222;
  letter-spacing: 1.8px; }

p {
  line-height: 2.1; }
  p code {
    color: #333;
    background-color: #eee;
    padding: 0.1em 0.4em;
    border-radius: 3px; }

b,
strong {
  font-weight: 600; }

.jumbotron {
  margin-bottom: 0;
  text-align: center;
  background: url(../images/header.jpg) center center no-repeat #464646;
  -webkit-background-size: cover;
  background-size: cover;
  background-color: #00081a; }
  .jumbotron p {
    font-size: 1rem;
    color: white;
    font-weight: 500;
    margin-top: 10px; }
  .jumbotron img {
    height: 150px; }

.content {
  margin-bottom: 40px; }
  .content a img:hover {
    box-shadow: 0 4px 8px 2px rgba(0, 0, 0, 0.2);
    -webkit-transform: scale(1.01);
    transform: scale(1.01);
    -webkit-transition: all 0.3s;
    transition: all 0.3s; }

hr {
  margin-top: 3rem;
  margin-bottom: 3rem; }

.row .no-padding {
  padding: 0; }

.square_btn {
  display: inline-block;
  background-color: #0e4ead;
  color: #fff !important;
  font-size: 1em;
  width: 100%;
  text-align: center;
  line-height: 1.7;
  text-decoration: none !important;
  letter-spacing: 0.05em;
  padding: 0.7em 1em;
  border-radius: 3px;
  cursor: pointer;
  box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.14), 0 1px 5px 0 rgba(0, 0, 0, 0.12), 0 3px 1px -2px rgba(0, 0, 0, 0.2);
  -webkit-tap-highlight-color: transparent;
